t\t</NcButton>\n\t\t</NcNoteCard>\n\t</NcGuestContent>\n</template>\n\n<style module>\n.updaterAdminCli__button {\n\tmargin-block: 1rem;\n\tmargin-inline: auto;\n}\n</style>\n"],"sourceRoot":""}]);
